<template>
<div class="selector flex flex-column flex-1">
    <div class="selector-time flex flex-column flex-1">
        <span class="title flex-1">时间范围</span>
        <div class="selections flex flex-column flex-3">
            <div>
                <li v-for="(key, value) in timeItems" :key="value"  class="time-item" :class="{active: key}" @click="selectTime(value)">
                    {{value}}
                </li>
            </div>
        </div>
    </div> 
    <div class="selector-org-tree flex flex-column flex-3">
        <span class="title flex-1">时间范围</span>
        <vue-tree :tree-data="treeData" v-model="ids" :options="options" class="org-tree flex-9" />
    </div>
    <div class="selector-btn">
        <div class="selector-btn-cancle inactive">取消</div>
        <div class="selector-btn-confirm active">确认</div>
    </div>
</div>
</template>

<script>
import treeData from '../data/tree'
export default {
    data() {
        return {
            selectTIme: '本周',
            timeItems: {
                本周: true,
                本月: false,
                本季: false,
                本年: false,
                上周: false,
                上月: false,
                上季: false,
                上年: false
            },
            ids: [],
            options: {},
            treeData: treeData,
        }
    },
    methods: {
        selectTime(name) {
            for(var key in this.timeItems) {
                this.timeItems[key] = false;
            }
            this.timeItems[name] = true;
            this.selectTIme = name;
        }
    }
}
</script>

<style>
.selector {
    background-color: white;
    height: calc(100% - 120px);
    width: 100%;
    position: absolute;
    top: 120px;
    left: 0;
    border-radius: 10px;
    padding: 0 10px 0 10px;
    font-size: 13px;
}

.selector .title {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.selector .time-item {
    border-radius: 20px; 
    border: 1px solid grey;
    margin: 10px;
    float: left;
    width: 75px;
    line-height: 26px;
    height: 26px;
}

.selector .time-item.active {
    background-color: #039CE3;
    color: white;
    border: none;
}

.selector .selector-btn {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 0;
    height: 30px;
    width: 100%;
    font-size: 14px;
}

.selector .selector-btn .selector-btn-cancle {
    position: absolute;
    bottom: 10px;
    height: 30px;
    line-height: 30px;
    width: 70px;
    left: calc(50% - 70px);
    border-radius: 20px 0 0 20px;
}

.selector .selector-btn .selector-btn-confirm {
    position: absolute;
    bottom: 10px;
    height: 30px;
    line-height: 30px;
    width: 70px;
    left: calc(50%);
    border-radius: 0 20px 20px 0;
}

.selector .selector-btn-cancle.active,
.selector .selector-btn-confirm.active {
    background-color: #039CE3;
    color: white;
}

.selector .selector-btn-cancle.inactive,
.selector .selector-btn-confirm.inactive {
    border: 1px solid rgba(128, 128, 128, 0.521);
    color: black;
}
</style>
